.loading {
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
}

.loading.button {
  display: inline-flex;
  align-items: center !important;
  justify-content: center !important;
}

.loading.button .loader-spinner {
  width: 20px;
  height: 20px;
  margin: 0 !important;
}

.loading.button .text {
  font-size: 16px;
  margin-left: 10px;
}

.loading.x-small .loader-spinner {
  width: 30px;
  height: 30px;
}

.loading.x-small .text {
  font-size: 12px;
}

.loading.small .loader-spinner {
  width: 40px;
  height: 40px;
}

.loading.small .text {
  font-size: 14px;
}

.loading.medium .loader-spinner {
  width: 50px;
  height: 50px;
}

.loading.medium .text {
  font-size: 18px;
}

.loading.large .loader-spinner {
  width: 90px;
  height: 90px;
}

.loading.large .text {
  font-size: 24px;
}

.loading.inverse .loader-spinner {
  border-top: 2px solid rgba(0, 123, 255, 0.9);
  border-right: 2px solid rgba(0, 123, 255, 0.9);
  border-bottom: 2px solid rgba(0, 123, 255, 0.9);
  border-left: 2px solid rgba(0, 123, 255, 0.2);
}

.loading.inverse .text {
  -webkit-animation: pulse-light 2500ms infinite linear;
  -moz-animation: pulse-light 2500ms infinite linear;
  -ms-animation: pulse-light 2500ms infinite linear;
  -o-animation: pulse-light 2500ms infinite linear;
  animation: pulse-light 2500ms infinite linear;
}

.loading .loader-spinner {
  display: inline-block;
  text-indent: -12345px;
  border-top: 2px solid rgba(232, 74, 95, 0.9);
  border-right: 2px solid rgba(232, 74, 95, 0.9);
  border-bottom: 2px solid rgba(232, 74, 95, 0.9);
  border-left: 2px solid rgba(232, 74, 95, 0.2);
  border-radius: 50%;
  -webkit-animation: spinner 700ms infinite linear;
  -moz-animation: spinner 700ms infinite linear;
  -ms-animation: spinner 700ms infinite linear;
  -o-animation: spinner 700ms infinite linear;
  animation: spinner 700ms infinite linear;
}

.loading .text {
  margin: 10px 0px 0px;
  -webkit-animation: pulse-dark 2500ms infinite linear;
  -moz-animation: pulse-dark 2500ms infinite linear;
  -ms-animation: pulse-dark 2500ms infinite linear;
  -o-animation: pulse-dark 2500ms infinite linear;
  animation: pulse-dark 2500ms infinite linear;
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-moz-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-o-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes pulse-dark {
  0% {
    color: rgba(0, 0, 0, 0.2);
  }

  50% {
    color: rgba(0, 0, 0, 0.8);
  }

  100% {
    color: rgba(0, 0, 0, 0.2);
  }
}

@-moz-keyframes pulse-dark {
  0% {
    color: rgba(0, 0, 0, 0.2);
  }

  50% {
    color: rgba(0, 0, 0, 0.8);
  }

  100% {
    color: rgba(0, 0, 0, 0.2);
  }
}

@-o-keyframes pulse-dark {
  0% {
    color: rgba(0, 0, 0, 0.2);
  }

  50% {
    color: rgba(0, 0, 0, 0.8);
  }

  100% {
    color: rgba(0, 0, 0, 0.2);
  }
}

@keyframes pulse-dark {
  0% {
    color: rgba(0, 0, 0, 0.2);
  }

  50% {
    color: rgba(0, 0, 0, 0.8);
  }

  100% {
    color: rgba(0, 0, 0, 0.2);
  }
}

@-webkit-keyframes pulse-light {
  0% {
    color: rgba(255, 255, 255, 0.2);
  }
  50% {
    color: rgba(255, 255, 255, 0.8);
  }

  100% {
    color: rgba(255, 255, 255, 0.2);
  }
}

@-moz-keyframes pulse-light {
  0% {
    color: rgba(255, 255, 255, 0.3);
  }

  50% {
    color: white;
  }

  100% {
    color: rgba(255, 255, 255, 0.3);
  }
}

@-o-keyframes pulse-light {
  0% {
    color: rgba(255, 255, 255, 0.3);
  }

  50% {
    color: white;
  }

  100% {
    color: rgba(255, 255, 255, 0.3);
  }
}

@keyframes pulse-light {
  0% {
    color: rgba(255, 255, 255, 0.3);
  }

  50% {
    color: white;
  }

  100% {
    color: rgba(255, 255, 255, 0.3);
  }
}
